import React, { Component } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import './Header.css';
import Button from 'react-bootstrap/Button';



class HomeCarousel extends Component {
    constructor(props){
           super(props);

        this.state = {
            
        }
    }
    render() {
        return (
            <div>
                <Carousel>
                    <Carousel.Item className="carouselBx">
                        <img className="d-block w-100 imgsize" src="https://i.ibb.co/rsh6tmK/hotel-room.jpg" alt="FirstSlide"/>
                        <Carousel.Caption>
                            <div className="carouselBx2">
                                <p className="carouselBx3 mb-3">Nanet Hotels</p>
                                <p className="carouselBx4">We offer superior hospitality services</p><br/>
                                <Button variant="primary"  onClick={()=>this.props.history.push('/')}>Contact Us</Button>{' '}
                            </div>
                            
                        </Carousel.Caption>
                    </Carousel.Item>
                    
                    <Carousel.Item>
                        <img className="d-block w-100 imgsize" src="https://i.ibb.co/nMrfcYm/nollywood-restaurant.jpg" alt="FirstSlide"/>
                        <Carousel.Caption>
                            <div className="carouselBx2">
                                <p className="carouselBx3 mb-3"> Nanet Restaurants</p>
                                <p className="carouselBx4 mb-3">We provide catering services to corporate organisations</p>
                                <Button variant="warning" onClick={()=>this.props.history.push('/')}>Find a Restaurant</Button>{' '}
                            </div>
                            
                        </Carousel.Caption>
                    </Carousel.Item>

                    <Carousel.Item>
                        <img className="d-block w-100 imgsize" src="https://i.ibb.co/Kz2McRg/nanet-meatpie-restaurant.jpg" alt="FirstSlide"/>
                        <Carousel.Caption>
                            <div className="carouselBx2">
                                <p className="carouselBx3 mb-3"> Nanet Entertainment</p>
                                <p className="carouselBx4 mb-3">We are in the business of entertainment.</p>
                                <Button variant="danger" onClick={()=>this.props.history.push('/')}>Get in touch</Button>{' '}
                            </div>
                            
                        </Carousel.Caption>
                    </Carousel.Item>
                    
                </Carousel>
            </div>
        );
    }
    
}



export default HomeCarousel;