import React, { Component } from 'react';
import './nanet.css';
import Button from 'react-bootstrap/Button';
import Offcanvas from 'react-bootstrap/Offcanvas';
import CloseButton from 'react-bootstrap/CloseButton';
import Card from 'react-bootstrap/Card';



class About extends Component {
    constructor(props){
        super(props);
        console.log(">>> inside nanetConstructor", props)
        this.state={
            show:false
        }
    }

    handleShow(){
        this.setState({show:true})
    }

    handleClose(){
        this.setState({show:false})
    }
    
    render() {
        return (
            <>
                <div className="pageHead">
                   
                    <img src="https://i.ibb.co/X35fv7f/club.jpg" className="editPix" alt="aboutPix"/>
                    <center><p className="textedi">About Us</p></center>
                    
                </div>
                <div className="picboard">
                    <div className ="foto">
                        <img className="editfoto"src="https://i.ibb.co/hKGWd7c/nollywood-restaurant.jpg" alt="foto"/>
                    </div>
                    <div className ="textboard">
                        <p className=" mb-3">
                            Nanet Hotels Limitetd is a hospitality management company that has been in the business since 1970. During this period it has developed infrastructure and systems and accumulated a wealth of experience and goodwill in the management services in the industry. We have operated several facilities around the country and received numerous accolades and awards, both nationally and internationally. 
                        </p>
                        <p className="cut mb-3">
                            The most recent of which is the International Awards for Tourist, Hotel, Restaurant and Catering Industry. Received in January 2012 at Madrid, Spain. We have also received The Millenium Award for Quality. Received May 2008 at Geneva, Switzerland.
                        </p>

                        <p className="cut mb-3">
                            Nanet has been involved with over 50 hospitality business outlests accross Nigeria and managed for both government and private sector investors.We are providers of specialised consultancy, development, supply and management services in the hospitality industry.
                        </p>

                        <p className="mb-3">
                            At Nanet Hotels Limited, we guarantee a financial return on investment (ROI), trusted maintenance of assets and added value to investors business name and business location. 
                            Please accept a warm welcome to our world of superior hospitality services as you navigate our services.
                        </p>


                    </div>
                    
                    <center><img src= "https://i.ibb.co/SscZnxp/Logo.png" className="logo2" style={{width:"40px", height:"40px"}} alt="logo"/></center>
                    <center>
                        <div className="md">
                            <img className="image" src= "https://i.ibb.co/h2W5xcy/Ini.jpg" alt="md"/>
                        </div>
                        <div className="md2">
                            <h5>Ini Akpabio</h5>
                            <h6>Chairman</h6>
                        </div>
                        
                        <div className='md3'>
                            <img className="md3b" src="https://i.ibb.co/KD4zsKj/signature.png" alt="signature"/>
                        </div>

                    </center>
                    <div>
                        <Button variant="outline-primary mb-3 mission" onClick={()=>this.handleShow()}>Mission Statement</Button>{' '}

                    </div>
                    
                </div>

                <Offcanvas show={this.state.show}>
                    
                    <div className="missionvission">
                        <Offcanvas.Body>
                            <div className="size13">
                                <CloseButton onClick={()=>this.handleClose()}/>
                            </div>
                            <div className="missionStatement">
                                <hr/>
                                    
                                <Card.Title>
                                    Nanet vission Statement
                                </Card.Title>
                                <Card.Subtitle className="mb-2 text-muted">
                                    Our Vision
                                </Card.Subtitle>
                                <Card.Text>
                                To become a leading and internationally acclaimed hospitality chain offering World Class Management for advancement of Tourism.
                                </Card.Text>
                                    
                              
                                <br/><br/>
                                <hr/>
                                  
                                <Card.Title>
                                    Nanet Mission Statement
                                </Card.Title>
                                <Card.Subtitle className="mb-2 text-muted">
                                    Our Mission
                                </Card.Subtitle>
                                <Card.Text>
                                    At Nanet Hotels Limited,we will always go to the extra mile to meet and,in fact, exceed the changing needs and expectations of our valued guests through the professional delivery of distinctively superior quality products and services that give real value for money. 
                                </Card.Text>

                                <hr/>
                                        
                            </div>
                            
                        </Offcanvas.Body>
                    </div>
                    
                </Offcanvas>
                
            </>
        );
    }
}



export default About;