import React from 'react';
import './Header.css';


const Footer = () => {
    return(
        <div>
            <hr/>
            <div>
                <center>
                    <h6 style={{color:'tomato'}}>&copy; Nanet Group&nbsp;({new Date().getFullYear()})</h6>
                    <i className="fa fa-map-marker"></i> &nbsp;Nanet Mahuta Beside Seat of Mercy Plaza, Yakowa Express Way, Kaduna, Nigeria


                    <p><i className="fa fa-phone"></i>&nbsp;+234 803 678 2821 |  +234 817 216 2633</p>
                    <div>
                        <span classname="twitr">
                            <a href="http://www.twitter.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i class="fab fa-twitter"></i>
                            </a>
                        </span>&nbsp; &nbsp;
                    
                    
                        <span classname="facebk">
                            <a href="http:www.facebook.com" target='_blank' rel="noopener noreferrer" aria-label='facebook'>
                                <i class="fab fa-facebook"></i>
                            </a>
                            
                        </span>&nbsp; &nbsp;
                    
                    
                        <span classname="insta">
                            <a href="http://www.instagram.com" target='_blank' rel="noopener noreferrer" aria-label='insta'>
                                <i class="fab fa-instagram"></i>
                            </a>
                        </span>
                    </div>
                </center>
                
            </div>
        </div>
    )
}

export default Footer;