import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import './services.css';


const servicesUrl = "https://nanetapi.herokuapp.com/services";

class Services extends Component {
   constructor(props){
        super(props)
        console.log(">>>Inside RMConstructor")
        this.state={

            services:'',
        }
    }

    getServices=((data)=>{
        if(data){
            return data.map((item)=>{
                return(
                    <>
                        
                            <Card className="editCard">
                                <Card.Img variant="top" src={item.image} className="editcardimage"/>
                                <Card.Body>
                                    <Card.Title>
                                        {item.Heading}
                                    </Card.Title>
                                    <Card.Text>
                                        <p><h7>{item.type1}</h7></p>
                                        <p><h7>{item.type2}</h7></p>
                                        <p><h7>{item.type3}</h7></p>
                                        <p><h7>{item.type4}</h7></p>
                                    </Card.Text>
                                    <Button variant="primary" onClick={()=>this.props.history.push('/')}>More Info</Button>
                                </Card.Body>

                            </Card>
                        
                        
                        
                    </>
                )
            })
        }
    })

    

    render() {
        console.log(">>> Inside RMrender", this.state)
        return (
            <>
                <div className="services">
                    <center><img className="imageresize" src="https://i.ibb.co/vPvqq4x/fountain-hall-2.jpg" alt="service"/></center>
                </div>
                <div className="imagetext">
                    <center><p className="texthere">Services</p></center>
                </div>
                <div className="quote1">
                    <p className="quoteStyle">"A satisfied customer is the best business strategy of all" -Michael LeBoeuf</p>
                </div>
                <div className="cardsEdit mt-2">
                    <center><h2 className="mb-3">Our Services</h2></center>
                    {this.getServices(this.state.services)}
                </div>
            </>
        );
    }

    componentDidMount(){
        console.log (">>> Inside RMdidMount")
        fetch(`${servicesUrl}`, {method:'GET'})
        .then((resp) => resp.json())
        .then((data) => {
            this.setState({
                services:data
            })
        
        })
    }
}



export default Services;